.simple-card {
  display: inline-block;
  width: 16%;

  .ant-card-meta-description {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .simple-card-preview {
    object-fit: cover;
    width: 100%;
  }
}

@media (max-width: 1280px) {
  .simple-card {
    width: 25%;
  }
}


@media (max-width: 992px) {
  .simple-card {
    width: 33%;
  }
}

@media (max-width: 576px) {
  .simple-card {
    width: 100%;
  }
}
