.content-wrap {
  display: flex;
  .border-bottom {
    &:after {
      content: '';
      width: 100%;
      display: block;
      border-bottom: 1px solid #d9d9d9;
      margin: 10px 0;
    }
  }

  .sidebar {
    flex-basis: 250px;
    background-color: #fff;
    padding: 20px;
    border-right: 1px solid #d9d9d9;
    .main-title {
      display: flex;
      border-bottom: 1px solid #d9d9d9;
      margin-bottom: 5px;
      padding-bottom: 5px;
      div {
        flex: 1;
        font-size: 1.2em;
      }
    }

    .sidebar-filter {
      .sidebar-filter-title {
        margin: 10px 0 5px 0;
        font-weight: bold;
      }
      .sidebar-filter-item {
      }
    }
  }

  .content {
    width: 100vw;
    padding: 20px;

    .pagination-wrap {
      display: flex;
      .pagination {
      }
      .sort {
        margin-right: 10px;
      }
    }

    .simple-card {
      padding: 0 10px 10px 0;
    }
  }
}

@media (max-width: 992px) {
  .content-wrap {
    .content {
      .pagination-wrap {
        flex-direction: column;
        .sort {
          margin-bottom: 10px;
        }
      }
    }
  }
}

@media (max-width: 768px) {
  .content-wrap {
    .sidebar {
      display: none;
    }
  }
}
