.default-layout {
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;

  .default-header {
    display: flex;
    flex-basis: 40px;
    padding: 10px 20px;
    position: sticky;
    top: 0;
    z-index: 10;
    border-bottom: 1px solid #d9d9d9;
    background-color: #fff;

    .search {
      width: 300px;
    }
  }

  .default-content-layout {
  }
}
