.item-detail {
  .section {
    background-color: #fff;
    border: 1px solid #ecedef;
    padding: 1.5rem;
    border-radius: 4px;

    h4 {
      margin-bottom: 15px;
    }
  }

  .item-head {
    background-color: #282a35;
    padding-top: 105px;
    height: 420px;

    .item-head-content {
      display: flex;
      margin: 0 auto;
      width: 60vw;
      background-color: rgba(40, 42, 53, 0.9);

      .item-preview {
        img {
          width: 260px;
        }
      }
      .item-info {
        margin-left: 2vw;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .info {
          margin-bottom: 3vh;

          h1, h3 {
            color: #fff;
          }
        }
        .trailer {
          a:first-child {
            margin: 0 0.5vw 0.5vh 0;
          }
        }
      }
    }
  }

  .item-content {
    display: flex;
    margin: 12vh auto;
    width: 60vw;

    .main {
      flex: 1;

      .characters {
        .character {
          padding: 15px;
          display: flex;
          align-items: center;
          border-bottom: 1px solid #ecedef;
          &:last-child {
            border-bottom: none;
          }

          .preview img {
            object-fit: cover;
            height: 16vh;
            width: 12vh;
          }
          .info {
            margin-left: 1.5vw;
            max-height: 16vh;
            overflow-y: auto;
          }
        }
      }
    }

    .side {
      flex-basis: 20vw;
      margin-left: 1.5vw;
      & > div {
        margin-top: 1.5vh;
        &:first-child {
          margin-top: 0;
        }

        .recommendations {

        }
      }
    }
  }
}

@media (max-width: 1280px) {
  .item-detail {
    .item-content {
      flex-direction: column-reverse;

      .side {
        margin-left: 0;
        margin-bottom: 1.5vh;
        & > div {
          margin-top: 1.5vh;
        }
      }
    }
  }
}
